<template>
    <div>
        <div id="login_wrapper"  style="margin:0;">
            <div class="login_box topspace">
                <div class="login_box_inner">
                    <h1>비밀번호 찾기</h1>
                    
                    <div class="address"><input type="text" class="login_input" v-model="member.memberPhone" placeholder="전화번호(숫자만 입력해주세요)"  @keypress="onlyNumber"  @input="changePhone" maxlength="11"><span><a  @click="certifyTrans">인증번호전송</a></span></div>
                    <div class="address"><input type="text" class="login_input" v-model="certifyNumber" placeholder="인증번호입력" @keypress="onlyNumber" maxlength="6" ><span><a  @click="certifyCertify">인증하기</a></span></div>


                    <div><input type="text" class="login_input" v-model="member.id" placeholder="아이디 입력"></div>
                    <div><input type="password" class="login_input" v-model="member.newPassword" placeholder="새 비밀번호(6~24자리)"></div>
                    <div><input type="password" class="login_input" v-model="newRePassword" placeholder="비밀번호 확인(6~24자리)"></div>



                    <button class="btn_login" style="margin-top:30px;" @click="submit">비밀번호 변경</button>


                </div>
            </div>
            <div class="login_copyright">@2020 JANGLAEBACSA ALL RIGHTS RESERVED.</div>
        </div>
    </div>
</template>

<script>

import '@/assets/js/jquery-1.9.1.min.js';
import '@/assets/js/ux_common.js';
import myMixin from '@/mixin/mixin'
import { mapGetters } from 'vuex'
export default {
  name: 'SearchId',
  mixins: [myMixin],
  components: { 
	},
    data(){
        return{
			member:{
				id:'',
				password:'',
				newPassword:''
			},
			newRePassword:'',
            certifyBeforeNumber:'',
            certifyTransFlag:false,
            certifyCertifyFlag:false,
            certifyNumber:''
        }
    },
  	props: {
  	},
	computed:{
		...mapGetters(['getMemberIdSearchList']),
	},
   	mounted() {
      //let recaptchaScript = document.createElement('script')
      //recaptchaScript.setAttribute('src', 'https://www.google.com/recaptcha/api.js')
      //document.head.appendChild(recaptchaScript)
    },
    beforeDestroy(){
        this.$store.commit('MEMBER_SEARCH_INIT')
    },
    methods:{
        changePhone(){
            
            this.certifyTransFlag = false
            this.certifyBeforeNumber = ''
            this.certifyCertifyFlag = false
        },
        certifyCertify(){
            if(this.certifyBeforeNumber+"" !== this.certifyNumber+""){
                alert("인증번호가 일치하지 않습니다.");
            }else{
                this.certifyCertifyFlag = true
                alert('인증되었습니다.')
            }

        },
        certifyTrans(){
            let result = Math.floor(Math.random() * 1000000)+100000;
            if(result>1000000){
                result = result - 100000;
            }
            if(!this.phoneCheck(this.member.memberPhone)){

                return;
            }
    
            this.certifyBeforeNumber = result
            
            const parameter={
                param: {
                    phone:this.member.memberPhone,
                    certifyNumber:  this.certifyBeforeNumber
                }
            }
	  	    this.$store.dispatch("REQUEST_MEMBER_CERTIFY", parameter).then(data => {
                  
				if (data.result) {
                    alert("인증번호를 전송하였습니다.")
                    this.certifyTransFlag = true
					
				}
			})

        },
        validataion(){
            if(!this.certifyTransFlag && !this.certifyCertifyFlag){
                alert("인증을 진행해 주세요.");
                return
            }

            return true;
            
        },
        submit(){
            if(!this.validataion()){
                return
            }
           if (this.member.newPassword === null || this.member.newPassword === '') {
				alert("새 비밀번호를 입력하세요")
				return;
			}
			if (this.member.newPassword !== this.newRePassword) {
				alert("암호를 다시 확인하세요")
				return;
			}
			const parameter = {
				id: this.member.id,
				password: this.member.password,
				newPassword: this.member.newPassword
			}
			this.$store.dispatch('REQUEST_CHANGE_PASSWORD2', parameter)
        }
    }
}
</script>

